@use 'base/variables' as v;
@use 'base/mixins' as m;

.nav {
    background-color: v.$primario;
    margin: 2rem 0;
    padding: .3rem 0;

    &__border {
        padding: 1.7rem 0;
        border-block: .2rem dashed v.$blanco;
    }
    &__contenedor {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet{
            flex-direction: row;
            justify-content: space-between;
        }
    }
    &__link {
        color: v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1.2rem;

        &:last-of-type {
            margin-bottom: 0;
        }
        @include m.tablet{
            margin-bottom: 0;
        }
    }
}